<template>
    <div class="newsite">
        <!-- 头部开始 -->
        <header>
          <Head>
            <div slot='a' class="he login_he">
              <div class="left">
                <a-Icon type="left" @click="$router.push('/Redact')" style="line-height: 0.5rem" />
              </div>
              <div class="content" style="line-height: 0.5rem">新增地址</div>
            </div>
          </Head>
        </header>
        <!-- 头部结束 -->

        <div class="site">
            <!-- 姓名 -->
            <input type="text" placeholder="请填写你的姓名" v-model='username' />
            <!-- 小区、写字楼、学校 -->
            <input type="text" placeholder="小区/写字楼/学校等" @click="ssite()" v-model='school' />
            <!-- 详细 -->
            <input type="text" placeholder="请填写详细送餐地址" v-model='site' />
            <!-- 手机号 -->
            <input type="text" placeholder="请填写能够联系到您的手机号" v-model='cellphone' />
            <!-- 备用电话 -->
            <input type="text" placeholder="备用联系电话(选填)" v-model='cellnumber' />
        </div>

        <button @click='add()'>新增地址</button>

    </div>
</template>

<script>
import axios from 'axios';    
import { Dialog } from 'vant'
import Head from '@/page/header/Head'    
export default{
    name:'Newly',
    components:{Head},
    data() {
        return {
            [Dialog.Component.name]: Dialog.Component,
            username:'', // 姓名
            school:'', // 小区、写字楼、学校
            site:'', // 详细送餐地址
            cellphone:'', // 手机号
            cellnumber:'', // 备用手机号 
            geohash:''
        }
    },
    created() {
        this.school = this.$route.query.name
        this.geohash = this.$route.query.geohash
    },
    methods: {
        add(){
            if(this.username==''&&this.school==''&&this.site==''&&this.cellphone==''){
                Dialog.alert({
                    message: '地址信息错误'
                })
            }else{
                // var site = {username:this.username,school:this.school,site:this.site,cellphone:this.cellphone,cellnumber:this.cellnumber}
                axios.post('http://elm.cangdu.org/v1/users/38715/addresses',
                    {
                        address:this.site,
                        address_detail:this.school,
                        name:this.username,
                        phone:this.cellphone,
                        phone_bk:this.cellnumber,
                        poi_type:0,
                        sex:1,
                        tag:'公司',
                        tag_type:4,
                        geohash:this.geohash
                    }
                ).then((res)=>{
                    // console.log(res)
                    this.$router.push('/Redact')
                })
                
            }
        },
        ssite(){
            this.$router.push('/Ssite')
        }
    },
}
</script>

<style lang="scss">
.newsite{
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    .site{
        width: 100%;
        height:4rem;
        background:white;
        margin-top:0.2rem;
        input{
            width: 80%;
            height: 0.6rem;
            border: none;
            outline: none;
            background: #F5F5F5;
            border: 1px solid gainsboro;
            margin-left: 10%;
            font-size: 0.24rem;
            color: black;
            padding: 0rem 0.2rem;
            margin-top: 0.15rem;
            border-radius: 0.05rem;
        }
    }
    button{
        width: 80%;
        height: 0.6rem;
        border: none;
        outline: none;
        background: #4CD964;
        color: white;
        font-size: 0.24rem;
        text-align: center;
        line-height: 0.6rem;
        border-radius: 0.05rem;
        margin-left: 10%;
        margin-top: 0.2rem;
    }
}
</style>


